import React, { FunctionComponent } from "react"
import styled from "@emotion/styled"
import { widths, breakpoints, colors } from "../../styles/variables"
import { rem } from "../../styles/mixins"
import {reveal_initialize} from '../../hooks/useRevealInit';
import 'reveal.js/dist/reveal.js';
import 'reveal.js/dist/reveal.css';
import 'reveal.js/dist/theme/solarized.css';
import { graphql, useStaticQuery } from "gatsby"
import { Color } from "../../components/intro/color"
import { Highlight } from "../intro/highlight";

export const Presentation: FunctionComponent = () => {

	const data = useStaticQuery(graphql`
		query Fridge{
			image: file(relativePath: { eq: "smartkitchen.webp" }) {
			childImageSharp {
				fluid {
				...GatsbyImageSharpFluid
				}
			}
			}
			image1: file(relativePath: { eq: "Samsung.jpg" }) {
				childImageSharp {
					fluid {
					...GatsbyImageSharpFluid
					}
				}
			}
			image2: file(relativePath: { eq: "LG.jpg" }) {
				childImageSharp {
					fluid {
					...GatsbyImageSharpFluid
					}
				}
			}
			image3: file(relativePath: { eq: "harvard.png" }) {
				childImageSharp {
					fluid {
					...GatsbyImageSharpFluid
					}
				}
			}
			image4: file(relativePath: { eq: "usahealth.png" }) {
				childImageSharp {
					fluid {
					...GatsbyImageSharpFluid
					}
				}
			}
			image5: file(relativePath: { eq: "leeds.png" }) {
				childImageSharp {
					fluid {
					...GatsbyImageSharpFluid
					}
				}
			}
			image6: file(relativePath: { eq: "FRI.jpg" }) {
				childImageSharp {
					fluid {
					...GatsbyImageSharpFluid
					}
				}
			}
			image7: file(relativePath: { eq: "JCP.png" }) {
				childImageSharp {
					fluid {
					...GatsbyImageSharpFluid
					}
				}
			}
			image8: file(relativePath: { eq: "ucc.png" }) {
				childImageSharp {
					fluid {
					...GatsbyImageSharpFluid
					}
				}
			}
			image9: file(relativePath: { eq: "trento.jpeg" }) {
				childImageSharp {
					fluid {
					...GatsbyImageSharpFluid
					}
				}
			}
			image10: file(relativePath: { eq: "maa.jpg" }) {
				childImageSharp {
					fluid {
					...GatsbyImageSharpFluid
					}
				}
			}
			image11: file(relativePath: { eq: "app.jpg" }) {
				childImageSharp {
					fluid {
					...GatsbyImageSharpFluid
					}
				}
			}
			image12: file(relativePath: { eq: "dataset.png" }) {
				childImageSharp {
					fluid {
					...GatsbyImageSharpFluid
					}
				}
			}
	}
	`)

 return  <>
	
	<div className = "reveal" style={{"width":"100%", "height":"480px"}}>
		<br/>
		<br/>
		<br/>
		<br/>
		<br/>
		<br/>
		<br/>
		<br/>
		<div className = "slides">
			<section data-background-color="rgb(31, 42, 60)">
				<h3>Take a tour to learn the story of</h3>
				<h2 style={{fontFamily:"Cutive Mono, monospace", color:"cornsilk"}}>REBRIDGERATOR</h2>
			</section>
			<section data-background-color="rgb(252, 114, 106)">
				<h1>I can't decide what to eat</h1>
			</section>
			<section data-background-color="rgb(231, 54, 85)" style={{fontFamily:"Cutive Mono, monospace"}}>
				<section>In this age of Internet of Things [IoT]
				<img src={data.image.childImageSharp.fluid.src} border="8"/>
				<br/>Our houses, especially kitchens, are becoming smarter already
				</section>
				<section>Samsung RS5300 can help recommend recipes per your personalized dietary preferences
				<img src={data.image1.childImageSharp.fluid.src} border="8" width="650px"/>
				<br/>it can create meal plans based on your current inventory, and prepare shopping lists, generate recipes based on inventory.
				</section>
				<section style={{fontSize:"38px"}}>LG InstaView Smart Fridges can detect the food inside the fridge,				
					<img src={data.image2.childImageSharp.fluid.src} border="8" width="550px"/>
				<br/>alerts users when some ingredients are going bad, suggests recipes based on what you have in stock, and writes shopping lists for you.
				</section>
				<section data-state="make-it-pop">What's in the fridge?<br/>What can I cook from it?<br/>What ingredients I am running out of?<br/>...<br/>...<br/>Is that all we can do?</section>
			</section>
			<section data-background-color="rgb(255, 255, 255)" style={{fontFamily:"Cutive Mono, monospace"}}>
			<section>
			<img src={data.image3.childImageSharp.fluid.src} border="8" width="250px"/>
			<img src={data.image4.childImageSharp.fluid.src} border="8" width="131px"/>
			<img src={data.image11.childImageSharp.fluid.src} border="8" width="150px"/>
			<img src={data.image6.childImageSharp.fluid.src} border="8" width="150px"/>
			<img src={data.image7.childImageSharp.fluid.src} border="8" width="200px"/>
			Nutritional psychiatry says what you <Color>eat</Color> has a lot to do with how you <Color>feel</Color>
			<br/><img src={data.image8.childImageSharp.fluid.src} border="8" width="350px"/>
			<img src={data.image9.childImageSharp.fluid.src} border="8" width="225px"/>
			<img src={data.image10.childImageSharp.fluid.src} border="8" width="323px"/>
			<img src={data.image5.childImageSharp.fluid.src} border="8" width="150px"/>
			</section>
			
			</section>
			<section data-background-color="rgb(231, 54, 85)" style={{fontFamily:"Cutive Mono, monospace"}}>
				<section>But let's not get ahead of ourselves...<br/><br/>We still need to figure out how exactly the <Color>food</Color> in the fridge is getting detected.</section>
				<section>Most object classifiers are quite well-trained on state-of-the-art <Color>convolutional</Color> <Color>neural</Color> <Color>networks</Color> like;<br/><br/>AlexNet, <Color>VGG</Color>, <Color>ResNet</Color>, DenseNet, <Color>Inception v3</Color>, GoogLeNet, MobileNet, EfficientNet <br/><br/>I picked the highlighted ones to train our data because of the speed of training and resources I had available to us. </section>
				<section>Where is the <Color>data</Color>?<br/><br/>Out of 8 publicly available datasets on grocery items, I found one most suitable to the application: <br/><br/>Freiburg Groceries Dataset by University of Freiburg</section>
				<section>It has 25 classes and ~5000 images;<br/>Beans, Cake, Candy, Cereal, Chips, Chocolate, Coffee, Corn, Fish, Flour<img src={data.image12.childImageSharp.fluid.src} border="8" width="750px"/><br/>Honey, Jam, Juice, Milk, Nuts, Oil, Pasta, Rice, Soda, Spices, Sugar, Tea, Tomato Sauce, Vinegar, Water</section>
				<section>So far <Color>VGG16</Color> is the fastest and least resource consuming model performing at 76.16% accuracy.<br/><br/>I plan to finetune it and play around with the learning rate scheduler more.</section>
				<section data-background-color="rgb(255, 255, 255)">But ingredients in the fridge are seldom organized and spaced out.<br/><br/>We need to detect and segment items/ingredients before we can classify them.</section>
			</section>
			<section data-background-color="rgb(255, 255, 255)">
				<section>Affectiva SDK</section>
			</section>
			<section data-background-color="rgb(231, 54, 85)">
				<section>Decision Tree</section>
			</section>
			<section data-background-color="rgb(255, 255, 255)">
				<section>Benefit</section>
			</section>
			<section data-background-color="rgb(231, 54, 85)">
				<section>Revenue</section>
			</section>
		</div>
	</div>
	{reveal_initialize()}
</>
}
